import { Component, Prop, Mixins } from 'vue-property-decorator'
import ContractRepository from '@/services/repositories/contract'
import { namespace } from 'vuex-class'
import ActionContract from './ActionContract.vue'
import PopupMixin from '@/mixins/popup'
import { RELATIONSHIP_VALUE_TO_TYPE } from '@/configs'
import PopupConfirm from '../../../audit/components/popup/PopupConfirm.vue'
import CreateCost from './CreateCost.vue'

const beUser = namespace('beUser')

@Component({ components: { PopupConfirm, ActionContract, CreateCost } })
export default class DetailContractHistory extends Mixins(PopupMixin) {
  @beUser.State('user') user!: any
  @Prop({ required: false, type: Object, default: {} }) contract!: Record<string, any>
  @Prop() dataPopupconfirm!: any
  query = {
    page: 1,
    limit: 20
  }
  contractAction: Record<string, any> = {}
  params: Record<string, any> = {}
  handleClose(): void {
    console.log('close test popup')
  }
  indexMethod(index: number): number {
    return (this.query.page - 1) * this.query.limit + index + 1
  }
  getRelationShip(code: number): string {
    return RELATIONSHIP_VALUE_TO_TYPE[code]
  }

  // created(): void {

  // }
}
