  getTimeToday(): void {
    const dd = String(this.dataInput.payFeeDate.getDate()).padStart(2, '0')
    const mm = String(this.dataInput.payFeeDate.getMonth() + 1).padStart(2, '0') //January is 0!
    const yyyy = this.dataInput.payFeeDate.getFullYear()
    this.dataInput.payFeeDate = yyyy + '-' + mm + '-' + dd
  }
  getTimeToday2(): void {
    const dd = String(this.dataInput.issueDate.getDate()).padStart(2, '0')
    const mm = String(this.dataInput.issueDate.getMonth() + 1).padStart(2, '0') //January is 0!
    const yyyy = this.dataInput.issueDate.getFullYear()
    this.dataInput.issueDate = yyyy + '-' + mm + '-' + dd
  }
  @Watch('dataInput.payFeeDate')
  checkPayFeeDate(value: string | number | any): void {
    const role = filter(this.user.permissionUserRoles, (elm: Record<string, any>) => elm.roleCode == 'ADMIN')
    if (role.length <= 0) {
      if (!this.dataProp.code) {
        let date = new Date()
        let timeT: any = date.setDate(date.getDate())
        if (this.user.permissionUserRoles.length == 1 && this.user.permissionUserRoles[0].roleCode == 'TRANSACTION_OFFICE_MEMBER') {
          let time: any = date.setDate(date.getDate() - 1)
          if (new Date(time).getDay() === 0) {
            time = date.setDate(date.getDate() - 2)
          }
          if (new Date(time).getDay() === 6) {
            time = date.setDate(date.getDate() - 1)
          }
          if (value < this.$options.filters?.formatDateTime(time).split('/').reverse().join('-')) {
            this.errorPayFeeDate = true
          } else {
            this.errorPayFeeDate = false
          }
        } else {
          if (date.getDate() == 1 || date.getDate() == 2 || (date.getDate() == 3 && new Date(timeT).getDay() === 1)) {
            this.errorPayFeeDate = false
          } else {
            let time: any = date.setDate(date.getDate() - 1)
            if (new Date(time).getDay() === 0) {
              time = date.setDate(date.getDate() - 2)
            }
            if (new Date(time).getDay() === 6) {
              time = date.setDate(date.getDate() - 1)
            }
            if (value < this.$options.filters?.formatDateTime(time).split('/').reverse().join('-')) {
              this.errorPayFeeDate = true
            } else {
              this.errorPayFeeDate = false
            }
          }
        }
      }
    }
  }
  @Watch('dataInput.feeType')
  checkFeeType(value: string | number): void {
    if (this.$route.name == 'dang-doi-soat' || (this.dataProp.code == 'edit' && (this.contractDetail.auditStatus == '8' || this.contractDetail.auditStatus == '2'))) {
      this.rules.applyFormNo[0].required = true
      this.rules.contractNo[0].required = true
      return
    }
    if (!this.dataProp.code) {
      if (value == '1') {
        this.rules.contractNo[0].required = false
        this.rules.applyFormNo[0].required = true
      } else {
        this.rules.contractNo[0].required = true
        this.rules.applyFormNo[0].required = false
      }
    } else {
      if (value == '1' && this.contractDetail.auditStatus == '1') {
        this.rules.contractNo[0].required = false
        this.rules.applyFormNo[0].required = true
      } else {
        this.rules.contractNo[0].required = true
        this.rules.applyFormNo[0].required = false
      }
    }
  }
  @Watch('dataInput.applyFormNo')
  checkContractNo(value: string): void {
    if (value.length == 10) {
      if (this.dataInput.customerChannel == 1) {
        if (value.slice(0, 2) != 'EA') {
          this.validateError = true
        } else {
          this.validateError = false
        }
      }
      if (this.dataInput.customerChannel == 2) {
        if (value.slice(0, 2) != 'LV') {
          this.validateError = true
        } else {
          this.validateError = false
        }
      }
    } else {
      this.validateError = false
    }
    if (!this.dataProp.code) {
      return
    }
    if (this.$route.name == 'dang-doi-soat' || (this.dataProp.code == 'edit' && (this.contractDetail.auditStatus == '8' || this.contractDetail.auditStatus == '2'))) {
      this.rules.applyFormNo[0].required = true
      this.rules.contractNo[0].required = true
      return
    }
    if (value && value != '0') {
      this.rules.applyFormNo[0].required = false
      //@ts-ignore
      this.$refs.dataInput?.clearValidate('applyFormNo')
    } else {
      if (this.dataInput.feeType == 2) {
        this.rules.applyFormNo[0].required = false
      } else {
        this.rules.applyFormNo[0].required = true
      }
    }
  }

  @Watch('dataInput.contractNo')
  checkApplyFormNo(value: string): void {
    if (!this.dataProp.code) {
      return
    }
    if (this.$route.name == 'dang-doi-soat' || (this.dataProp.code == 'edit' && (this.contractDetail.auditStatus == '8' || this.contractDetail.auditStatus == '2'))) {
      this.rules.applyFormNo[0].required = true
      this.rules.contractNo[0].required = true
      return
    }
    if (value && value != '0') {
      this.rules.applyFormNo[0].required = false
      //@ts-ignore
      this.$refs.dataInput?.clearValidate('applyFormNo')
    } else {
      this.rules.applyFormNo[0].required = true
    }
  }
  checkCustomerType = false
  @Watch('dataInput.customerType')
  checkType(value: number): void {
    if (value == 2) {
      this.openLoanCode = true
    } else {
      this.dataInput.loanCode = ''
      this.openLoanCode = false
    }
    if (value == 2 || value == 3 || value == 5) {
      this.rules.customerId[0].required = true
      this.rules.customerId[0].type = 'number'
    } else {
      delete this.rules.customerId[0].type

      this.rules.customerId[0].required = false
    }
    if (this.dataInput.customerType == 1 || this.dataInput.customerType == 4) {
      this.checkCustomerType = true
    } else {
      this.checkCustomerType = false
    }
  }
  @Watch('dataInput.numOfFeeYear')
  convertNumOfFeeYear(): void {
    if (isNaN(Number(this.dataInput.numOfFeeYear))) {
      this.dataInput.numOfFeeYear = ''
    } else {
      this.dataInput.numOfFeeYear = Number(this.dataInput.numOfFeeYear)
    }
  }
  @Watch('dataInput.numOfContractYear')
  convertNumOfContractYear(): void {
    if (isNaN(Number(this.dataInput.numOfContractYear))) {
      this.dataInput.numOfContractYear = ''
    } else {
      this.dataInput.numOfContractYear = Number(this.dataInput.numOfContractYear)
    }
  }
  @Watch('dataInput.currentFeeYear')
  convertCurrentFeeYear(): void {
    this.errorCurrentFeeYear = false
    if (this.dataInput.feeType == 1) {
      if (this.dataInput.currentFeeYear != 1) {
        this.errorCurrentFeeYear = true
      } else {
        this.errorCurrentFeeYear = false
      }
    }
    if (isNaN(Number(this.dataInput.currentFeeYear))) {
      this.dataInput.currentFeeYear = ''
    } else {
      this.dataInput.currentFeeYear = Number(this.dataInput.currentFeeYear)
    }
  }
  @Watch('dataInput.customerChannel')
  fillHSYCBH(): void {
    let name = this.dataInput.applyFormNo.slice(0, 2)
    if (this.dataInput.customerChannel == 1) {
      if (name == 'LV' || name != 'EA') {
        this.dataInput.applyFormNo = 'EA'
      }
    }
    if (this.dataInput.customerChannel == 2) {
      if (name == 'EA' || name != 'LV') {
        this.dataInput.applyFormNo = 'LV'
      }
    }
  }
  @Watch('dataInput.consultantMemberId')
  clearCertCode(id: number): void {
    if (!id) {
      this.dataInput.certCode = ''
    }
  }
  @Watch('dataInput.transactionOfficeId')
  fillGetRefMember(value: number): void {
    if (value) {
      this.getRefMember('')
    }
  }
  handleClose(): void {
    this.resetAllQuery()
    this.resetItems()
    if (this.$refs.dataInput) {
      //@ts-ignore
      this.$refs.dataInput.clearValidate()
    }
    if (this.$refs.formRule) {
      //@ts-ignore
      this.$refs.formRule.clearValidate()
    }
    this.setOpenPopup({
      popupName: 'create-cost',
      isOpen: false
    })
    this.$forceUpdate()
  }
  validateForm(): boolean {
    let formInput: any = this.$refs.dataInput
    let valid = false
    let validRule = false
    formInput.validate((_valid: boolean) => {
      valid = _valid
    })
    let formRule: any = this.$refs.formRule
    formRule.validate((_valid: boolean) => {
      validRule = _valid
    })
    return valid && validRule
  }
  handleCreate(): void {
    this.errorCategory = false
    if (this.dataInput.feeType == 1) {
      const ctr = filter(this.arrayCategory, (elm: Record<string, any>) => elm.code == this.formEmulationRule.dataTable[0].code)
      if (ctr.length > 0) {
        let main = 0
        let sub = 0
        forEach(this.formEmulationRule.dataTable, item => {
          if (item.code != ctr[0].code) {
            this.messErrorCategory = 'Không được nhập nhiều hơn 1 loại sản phẩm trên 1 hợp đồng'
            this.errorCategory = true
          }
          if (item.productType == 'MAIN') {
            main += 1
          }
          if (item.productType == 'SUB') {
            sub += 1
          }
        })
        if (this.dataProp.code == 'edit' && main != ctr[0].minMainProduct) {
          this.messErrorCategory = `Cần nhập ${ctr[0].minMainProduct} sản phẩm chính`
          this.errorCategory = true
        }
        if ((this.dataProp.code != 'edit' && main != ctr[0].minMainProduct) || (this.dataProp.code != 'edit' && sub < ctr[0].minSubProduct)) {
          this.messErrorCategory = `Cần nhập ${ctr[0].minMainProduct} sản phẩm chính và tối thiểu ${ctr[0].minSubProduct} sản phẩm phụ`
          this.errorCategory = true
        }
      }
    }
    this.errorCustomer = false
    if (this.dataInput.customerType == 1) {
      const customer = filter(this.arrayCustomer, (elm: Record<string, any>) => elm.id == this.dataInput.customerId)
      if (customer.length > 0) {
        if (customer[0].customerCifCode) {
          this.errorCustomer = true
        } else {
          this.errorCustomer = false
        }
      }
    }
    this.loadBtn = true
    const valid = this.validateForm()
    if (valid && !this.validateError && !this.errorCurrentFeeYear && !this.errorPayFeeDate && !this.errorCustomer && !this.errorCategory) {
      if (this.formEmulationRule.dataTable.length > 0) {
        forEach(this.formEmulationRule.dataTable, item => {
          if (typeof item.basicAmount === 'string') {
            item.basicAmount = parseFloat(item.basicAmount.replace(/,/g, ''))
          }
          if (typeof item.extraAmount === 'string') {
            item.extraAmount = parseFloat(item.extraAmount.replace(/,/g, ''))
          }
          if (!item.extraAmount) {
            item.extraAmount = 0
          }
        })
      }
      const data = {
        feeType: this.dataInput.feeType,
        customerChannel: this.dataInput.customerChannel,
        payFeeDate: this.dataInput.payFeeDate,
        issueDate: this.dataInput.issueDate,
        description: this.description,
        applyFormNo: this.dataInput.applyFormNo,
        loanCode: this.dataInput.loanCode,
        contractNo: this.dataInput.contractNo,
        numOfFeeYear: this.dataInput.numOfFeeYear,
        currentFeeYear: this.dataInput.currentFeeYear,
        numOfContractYear: this.dataInput.numOfContractYear,
        paymentPeriod: this.dataInput.paymentPeriod,
        customerType: this.dataInput.customerType,
        customerId: this.dataInput.customerId,
        bmbhId: this.dataInput.bmbhId,
        consultantMemberId: this.dataInput.consultantMemberId,
        salesMemberId: this.dataInput.salesMemberId,
        refMemberId: this.refMemberId,
        transactionOfficeId: this.dataInput.transactionOfficeId,
        productList: this.formEmulationRule.dataTable,
        consultantNo: this.dataInput.certCode,
        subContracts: this.formEmulationRule.dataTable,
        offerProgramId: this.dataInput.offerProgramId
      }
      if (this.dataProp?.code) {
        if (isNaN(this.dataInput.customerId)) {
          delete data.customerId
        }
        if (isNaN(this.dataInput.bmbhId)) {
          delete data.bmbhId
        }
        if (this.dataProp?.code == 'edit') {
          this.api
            .updateContract({ ...this.contractDetail, ...data })
            .then(() => {
              this.$emit('reload')
              this.$message.success('Cập nhật thông tin thành công')
              this.handleClose()
            })
            .catch(error => {
              console.log(error)
              this.$message.error('Cập nhật thông tin thất bại')
              this.handleClose()
            })
        }
        if (this.dataProp?.code == 'edit-auditing' || this.dataProp?.code == 'add') {
          const params = {
            paymentDiffId: this.dataProp.value.id,
            paymentSource: this.dataProp?.code === 'add' ? 'DAICHI' : null
          }
          let cloneProduct = [...this.formEmulationRule.dataTable]
          if (cloneProduct.length > 0) {
            forEach(cloneProduct, (item: Record<string, any>) => {
              item.auditStatus = '9'
            })
          }
          const cloneData = { ...data, productList: cloneProduct, subContracts: cloneProduct }
          const dataParam = {
            ...this.contractDetail,
            ...cloneData,
            auditStatus: '9'
          }
          this.api
            .updatePaymentDiff(params, dataParam, true)
            .then(() => {
              this.$emit('reload')
              this.$message.success('Cập nhật thông tin thành công')
              this.handleClose()
            })
            .catch(error => {
              console.log(error)
              this.$message.error('Cập nhật thông tin thất bại')
              this.handleClose()
            })
        }
      } else {
        this.api
          .AddCost(data)
          .then(() => {
            this.$emit('apply')
            this.$message.success('Thêm mới thành công')
            this.handleClose()
          })
          .catch(error => {
            console.log(error)
            this.loadBtn = false
            this.$message.error('Thêm mới thất bại')
            this.handleClose()
          })
      }
    } else if (this.errorCategory) {
      this.$message.error(this.messErrorCategory)
      this.loadBtn = false
    } else {
      this.$message.error('Nhập đầy đủ thông tin bắt buộc!')
      this.loadBtn = false
    }
  }
  async handleOpen(): Promise<void> {
    if (this.dataProp?.type === 'khach-hang') {
      this.dataInput.customerId = +this.$route.params.id
    }
    this.isLoading = true
    await Promise.all([this.getTimeToday(), this.getTimeToday2()])
    await Promise.all([this.checkCategoryProduct(), this.getTransactionOffice(''), this.getAllListCustomer(), this.getListOffer('')])
    await this.getTransOffice()
    if (this.dataProp?.type != 'audit') {
      await this.getSalesMember()
    }
    if (this.dataProp?.code == 'edit') {
      await this.getContractDetail()
      // this.$forceUpdate()
    }
    if (this.dataProp?.code == 'edit-auditing' || this.dataProp?.code == 'add') {
      await this.getDetailAuditing()
    }
    this.getIndex()
    this.initData()
    await this.getProductList()
    this.handleTotal()
    this.isLoading = false
  }
  checkCategoryProduct(): void {
    this.apiCategory.checkProductCategory().then((res: any) => {
      this.arrayCategory = res.data
    })
  }
  async getDetailAuditing(): Promise<void> {
    await this.api
      .getDetailPaymentDiff(this.dataProp.code !== 'add' ? this.dataProp.value.bankContractNo : this.dataProp.value.bankContractNo1, {
        paymentSource: this.dataProp.code !== 'add' ? 'LPB' : 'DAICHI'
      })
      .then(res => {
        if (res.bmbhFullName && !res.bmbhId) {
          this.nameBmbh = res.bmbhFullName
        }
        this.contractDetail = res
        this.dataInput.certCode = res.consultantNo
        this.dataInput.feeType = res.feeType
        this.dataInput.customerChannel = res.customerChannel
        this.dataInput.payFeeDate = this.$options.filters?.formatDateHourReverse(res.payFeeDate)
        this.dataInput.issueDate = this.$options.filters?.formatDateHourReverse(res.issueDate)
        this.description = res.description
        this.dataInput.contractNo = res.contractNo
        this.dataInput.applyFormNo = res.applyFormNo
        this.dataInput.loanCode = res.loanCode
        this.dataInput.numOfFeeYear = res.numOfFeeYear
        this.dataInput.currentFeeYear = res.currentFeeYear
        this.dataInput.numOfContractYear = res.numOfContractYear
        this.dataInput.currentContractYear = res.currentContractYear
        this.dataInput.paymentPeriod = res.paymentPeriod
        this.dataInput.customerType = res.customerType
        this.dataInput.customerId = res.customerId
        this.dataInput.beneficiaryName = res.beneficiaryName
        this.dataInput.consultantMemberId = res.consultantMemberId
        this.refMemberId = res.refMemberId
        if (res.transactionOfficeId) {
          this.dataInput.transactionOfficeId = res.transactionOfficeId
          this.saleMembersListTransactionOffice.unshift({
            locationCode: res.transactionOfficeCode,
            name: res.transactionOfficeName,
            id: res.transactionOfficeId
          })
          this.getAllListMemBer(res.transactionOfficeId)
          if (res.salesMemberId) {
            this.saleMembersList.unshift({
              memberNo: res.salesMemberNo,
              fullName: res.salesFullName,
              id: res.salesMemberId,
              phone: res.salesMobilePhone,
              refWorkPhone: res.salesWorkPhone,
              email: res.salesEmail
            })
            this.dataInput.salesMemberId = parseInt(res.salesMemberId)
          }
          if (res.refMemberId) {
            this.saleMembersListRefMember.unshift({
              memberNo: res.refMemberNo,
              fullName: res.refFullName,
              id: res.refMemberId,
              phone: res.refMobilePhone,
              refWorkPhone: res.refWorkPhone,
              email: res.refEmail
            })
          }
          if (res.consultantMemberId) {
            this.saleMembersListConsultantMember.unshift({
              memberNo: res.consultantMemberNo,
              fullName: res.consultantName,
              id: res.consultantMemberId
            })
          }
        }
        this.formEmulationRule.dataTable = res.subContracts
        forEach(this.formEmulationRule.dataTable, item => {
          const basicAmount: any = item.basicAmount
          const extraAmount: any = item.extraAmount
          item.basicAmount = this.$options.filters?.formatNumber(parseInt(basicAmount))
          item.extraAmount = this.$options.filters?.formatNumber(parseInt(extraAmount))
          return item
        })
        const elementCus: Record<string, any> = {
