            id: parseInt(res.bmbhId)
          }
          this.arrayBmbh.push(item)
          this.dataInput.bmbhId = parseInt(res.bmbhId)
        }
      }
      const key = 'id'
      // const listFilter = [...new Map(this.saleMembersList.map((item: Record<string, any>) => [item[key], item])).values()]
      // const listFilterRefMember = [...new Map(this.saleMembersListRefMember.map((item: Record<string, any>) => [item[key], item])).values()]
      // const listFilterConsultantMember = [...new Map(this.saleMembersListConsultantMember.map((item: Record<string, any>) => [item[key], item])).values()]
      const listFilterCustomer = [...new Map(this.arrayCustomer.map((item: Record<string, any>) => [item[key], item])).values()]
