  handleSizeChange(size: number): void {
    this.table.limit = size
  }
  summaryMethod(params: { columns: any }): any[] {
    const { columns } = params
    const sums: any[] = []
    forEach(columns, (column, index: number) => {
      if (index < 1) {
        sums[index] = ''
        return
      }
      if (index === 1) {
        sums[index] = 'Tổng'
        return
      }
      if (index === 2) {
        sums[index] = this.$options.filters?.formatNumber(this.totalBasicAmount)
        return
      }
      if (index === 3) {
        sums[index] = this.$options.filters?.formatNumber(this.totalBasicExtraAmount)
        return
      }
      if (index === 4) {
        sums[index] = this.$options.filters?.formatNumber(this.totalBasicAmount + this.totalBasicExtraAmount)
        return
      }
      if (index === 5) {
        sums[index] = this.$options.filters?.formatNumber(this.totalBasicAmount + (this.totalBasicExtraAmount * 10) / 100)
        return
      }
    })
    return sums
  }
