  created(): void {
    this.apiEmployee = getRepository('employee', {
      districtId: this.user?.districts?.[0].id
    })
    this.api = getRepository('contract', {
      districtId: this.user?.districts?.[0].id
    })
    this.apiParam = getRepository('param', {
      districtId: this.user?.districts?.[0].id
    })
    this.locationRes = getRepository('location', {
      districtId: this.districtId
    })
    this.apiDeal = getRepository('deal', {
      districtId: this.districtId
    })
    this.apiOffer = getRepository('offer', {
      districtId: this.districtId
    })
    this.apiCategory = getRepository('category', {
      districtId: this.districtId
    })
    EventBus.$on('pushIdCustomer', (data: any) => {
      this.arrayCustomer = []
      this.apiDeal
        .getCustomer({
          page: 1,
          limit: 20
        })
        .then(res => {
          this.arrayCustomer = res.data.content
          const customer = filter(res.data.content, (elm: Record<string, any>) => elm.id == data.id)
          if (customer.length > 0) {
            this.dataInput.customerId = data.id
          } else {
            this.arrayCustomer.push(data)
            this.dataInput.customerId = data.id
          }
        })
    })
    EventBus.$on('pushIdBmbh', (data: any) => {
      this.arrayBmbh = []
      this.apiDeal
        .getCustomer({
          page: 1,
          limit: 20
        })
        .then(res => {
          this.arrayBmbh = res.data.content
          const bmbh = filter(res.data.content, (elm: Record<string, any>) => elm.id == data.id)
          if (bmbh.length > 0) {
            this.dataInput.bmbhId = data.id
          } else {
            this.arrayBmbh.push(data)
            this.dataInput.bmbhId = data.id
          }
        })
    })
  }
