  async getProductList(): Promise<void> {
    await this.api.getProduct(this.queryDropProduct.queryProduct).then((res: any) => {
      this.arrayProduct = [...this.arrayProduct, ...res.data.content]
      if (this.dataProp.code == 'edit-auditing' || this.dataProp.code == 'edit' || this.dataProp.code == 'add') {
        const dataProduct = this.formEmulationRule.dataTable.map((item: Record<string, any>) => ({
          id: item.productId,
          productName: item.productName,
          productCode: item.productCode,
          totalAmount: item.totalAmount,
          extraAmount: item.extraAmount,
          basicAmount: item.basicAmount
        }))

        const data = [...this.arrayProduct, ...dataProduct]
        const listFilter = [...new Map(data.map((item: Record<string, any>) => [item['id'], item])).values()]
        this.arrayProduct = listFilter
      }
    })
  }

