      const result = await this.apiDeal.getCustomer({
        page: this.queryBmbh.page,
        limit: this.queryBmbh.limit
      })
      this.arrayCustomer = result.data.content
      this.arrayBmbh = result.data.content
    } catch (error) {
      console.log(error)
    }
  }

