  //get data
  async initData(): Promise<void> {
    await this.apiParam.getParamRelationship().then((res: any) => {
      this.optionsRelationship = res.data
    })
    await this.apiParam.getEmulationProgramSource().then((res: any) => {
      this.typeProgramSource = res.data
    })
    await this.apiParam.getEmulationApplyFeeTypel().then((res: any) => {
      this.typeApplyFee = res.data
    })
  }
  getRelationship(code: string): string {
    return RELATIONSHIP_TYPE[code]
  }
  getNameCustomerChannel(code: string): string {
    let name = ''
    if (code === 'DAICHI') {
      name = 'E-app'
    }
    if (code === 'BANK') {
      name = 'Truyền thống'
    }
    return name
  }
  handleTotal(): void {
    this.totalBasicAmount = 0
    this.totalBasicExtraAmount = 0
    forEach(this.formEmulationRule.dataTable, item => {
      if (item.basicAmount) {
        this.totalBasicAmount += parseFloat(item.basicAmount.replace(/,/g, ''))
      }
      if (item.extraAmount) {
        this.totalBasicExtraAmount += parseFloat(item.extraAmount.replace(/,/g, ''))
      }
    })
  }
  numberFormat(event: FocusEvent, index: number, name: keyof TableCost): void {
    const _event: any = event
    let fnumber = _event.target.value
    if (fnumber.length > 0) {
      if (fnumber === '-') {
        fnumber = parseInt(fnumber)
        if (!isNaN(fnumber)) {
          fnumber = this.$options.filters?.formatNumber(fnumber)
          _event.target.value = fnumber
          this.formEmulationRule.dataTable[index][name] = fnumber
        }
      }
      fnumber = fnumber.replaceAll(',', '')
      fnumber = parseInt(fnumber)
      fnumber = this.$options.filters?.formatNumber(fnumber)
      _event.target.value = fnumber
      this.formEmulationRule.dataTable[index][name] = fnumber
    }
    this.handleTotal()
  }
  getTotal(basicAmount: Record<string, any>, extraAmount: Record<string, any>): void {
    let sum = 0
    if (basicAmount) {
      sum += parseFloat(basicAmount.replace(/,/g, ''))
    }
    if (extraAmount) {
      sum += parseFloat(extraAmount.replace(/,/g, ''))
    }
    return this.$options.filters?.formatNumber(sum)
  }
  getFYP(basicAmount: Record<string, any>, extraAmount: Record<string, any>): void {
    let sum = 0
    if (basicAmount) {
      sum += parseFloat(basicAmount.replace(/,/g, ''))
    }
    if (extraAmount) {
      sum += (parseFloat(extraAmount.replace(/,/g, '')) * 10) / 100
    }
    return this.$options.filters?.formatNumber(sum)
  }
  getNameCustomer(customerCifCode: any, customerFullName: any): void {
    let name: any = ''
    if (customerCifCode && customerFullName) {
      name = customerCifCode + ' - ' + customerFullName.toUpperCase()
    } else if (customerFullName) {
      name = customerFullName.toUpperCase()
    }
    return name
  }
  getNameBmbh(customerCifCode: any, customerFullName: any): void {
    let name: any = ''
    if (customerCifCode && customerFullName) {
      name = customerCifCode + ' - ' + customerFullName.toUpperCase()
    } else if (customerFullName) {
      name = customerFullName.toUpperCase()
    }
    return name
  }
  getNameOffer(programCode: any, programName: any): void {
    let name: any = ''
    if (programCode) {
      name = programCode + ' - ' + programName
    } else {
      name = programName
    }
    return name
  }
  fillCode(): void {
    const code = filter(this.saleMembersListConsultantMember, (elm: Record<string, any>) => elm.id === this.dataInput.consultantMemberId)
    if (code.length > 0) {
      this.dataInput.certCode = code[0].certCode
    }
  }
  changeTransactionOfficeId(): void {
    this.dataInput.salesMemberId = ''
    this.dataInput.consultantMemberId = ''
    this.dataInput.certCode = ''
    if (this.dataInput.transactionOfficeId) {
      this.getAllIdMember(this.dataInput.transactionOfficeId)
    }
  }
  disabledExtraAmount(scope: any) {
    let disabled = false
    if (scope.row.productType == 'SUB' || scope.row.auditStatus === '9') {
      scope.row.extraAmount = 0
      disabled = true
    } else {
      disabled = false
    }
    return disabled
  }
  handleEditCustomer(id: number, e: any): void {
    e.stopPropagation()
    this.dataPropCustomer.id = id
    this.dataPropCustomer.title = 'Chỉnh sửa khách hàng'
    this.dataPropCustomer.code = 'edit'
    if (this.dataInput.customerType == 1 || this.dataInput.customerType == 4) {
      //ban su kien sang AddCustomer
      EventBus.$emit('checkNullCif')
    } else {
      EventBus.$emit('checkNullCif2')
    }
    this.setOpenPopup({
      popupName: 'addcustomer-popup',
      isOpen: true
    })
  }
  handleEditBmbh(id: number, e: any): void {
    e.stopPropagation()
    this.dataPropBmbh.id = id
    this.dataPropBmbh.title = 'Chỉnh sửa bên mua bảo hiểm'
    this.dataPropBmbh.code = 'edit'
    this.setOpenPopup({
      popupName: 'addbmbh-popup',
      isOpen: true
    })
  }
  handleFocusNameBmbh(): void {
    this.nameBmbh = ''
  }
  getAllListMemBer(locationId: number): void {
    this.apiEmployee
      .getEmployeeList({
        i: this.querySaleMember.i,
        ps: this.querySaleMember.ps,
        memberType: 'SALES',
        locationId: locationId
      })
      .then((res: any) => {
        this.saleMembersList = res.data
      })
      .catch((error: any) => {
        console.log(error)
        this.saleMembersList = []
      })
    this.apiEmployee
      .getEmployeeList({
        i: this.queryConsulantMember.i,
        ps: this.queryConsulantMember.ps,
        memberType: 'CONSULTANT',
        locationId: locationId
      })
      .then((res: any) => {
        this.saleMembersListConsultantMember = res.data
      })
      .catch((error: any) => {
        console.log(error)
        this.saleMembersListConsultantMember = []
      })
  }
