























import { Component, Mixins, Prop } from 'vue-property-decorator'
import getRepository from '@/services'
import { namespace } from 'vuex-class'
import PopupMixin from '@/mixins/popup'
import { forEach } from 'lodash'
import ContractRepository from '@/services/repositories/contract'
import EventBus from '@/utils/eventBus'

const beUser = namespace('beUser')
@Component({ components: {} })
export default class ActionContract extends Mixins(PopupMixin) {
  @Prop({ required: false, type: Object, default: {} }) action!: Record<string, any>
  @Prop({ required: false, type: Array, default: [] }) select!: Array<Record<string, any>>
  @Prop({ required: false, type: String, default: 'row' }) status!: string
  @Prop({ required: false, type: String, default: 'issue' }) type!: string

  @beUser.State('user') user?: Record<string, any>

  contract: Record<string, any> = {}
  apiContract!: ContractRepository
  params: any = {}
  rules: any = {
    valueDate: [
      {
        required: true,
        message: 'Vui lòng chọn ngày bàn giao HĐ',
        trigger: 'blur'
      }
    ]
  }
  dataAction: any = {
    valueDate: ''
  }

  handleOpen(): void {
    //@ts-ignore
    this.$refs['dataAction']?.resetFields()
    if (this.status === 'multi') {
      if (this.select.length > 1) {
        if (this.select[0].applyFormNo != this.select[1].applyFormNo) {
          this.dataAction.valueDate = ''
        } else if (this.select[this.select.length - 1].applyFormNo != this.select[this.select.length - 2].applyFormNo) {
          this.dataAction.valueDate = ''
        } else {
          this.dataAction.valueDate =
            this.type === 'date'
              ? this.$options.filters?.formatDateHourReverse(this.select[0].handOverDate)
              : this.$options.filters?.formatDateHourReverse(this.select[0].issueDate)
        }
      } else if (this.select.length == 1) {
        this.dataAction.valueDate =
          this.type === 'date'
            ? this.$options.filters?.formatDateHourReverse(this.select[0].handOverDate)
            : this.$options.filters?.formatDateHourReverse(this.select[0].issueDate)
      }
    } else {
      this.dataAction.valueDate =
        this.type === 'date' ? this.$options.filters?.formatDateHourReverse(this.action.handOverDate) : this.$options.filters?.formatDateHourReverse(this.action.issueDate)
    }
  }

  handleClose(): void {
    this.dataAction.valueDate = ''
    this.setOpenPopup({
      popupName: 'actionContract',
      isOpen: false
    })
    this.$emit('close')
    this.$emit('update')
  }
  handleSave(): void {
    let params: Record<string, any> = {}
    let contractNoArray: Array<any> = []
    let contractBankArray: Array<any> = []
    let contractNo = ''
    let bankContractNo = ''
    if (this.status === 'row') {
      params.bankContractNoArray = this.action.bankContractNo
      params.contractNoArray = this.action.contractNo
      if (this.type === 'date') {
        params.handOverDate = this.dataAction.valueDate
      } else {
        params.issueDate = this.dataAction.valueDate
      }
    } else {
      forEach(this.select, value => {
        if (value.contractNo) {
          contractNoArray.push(value.contractNo)
        }
        contractBankArray.push(value.bankContractNo)
      })
      bankContractNo = contractBankArray.join(',')
      contractNo = contractNoArray.join(',')
      params.bankContractNoArray = bankContractNo
      params.contractNoArray = contractNo
      if (this.type === 'date') {
        params.handOverDate = this.dataAction.valueDate
      } else {
        params.issueDate = this.dataAction.valueDate
      }
    }

    let a: any = this.$refs.dataAction
    console.log('param', params)
    const message = this.type === 'date' ? 'Cập nhật ngày bàn giao HĐ thành công' : 'Cập nhật ngày phát hành HĐ thành công'
    a.validate((valid: any) => {
      if (valid) {
        this.apiContract
          .updateAuditSelect(params)
          .then((res: Record<string, any>) => {
            this.$message.success(message)
            this.$emit('saveSuccess', res.data)
            this.$emit('reset')
            this.handleClose()
            EventBus.$emit('reloadDetail')
          })
          .catch(er => {
            this.$message.error(er.message)
          })
      } else {
        console.log('error validate data!')
        return false
      }
    })
  }

  created(): void {
    this.apiContract = getRepository('contract', {
      districtId: this.user?.districts?.[0].id
    })
  }
}
