


















































import PoppupMixin from '@/mixins/popup'
import { Component, Prop } from 'vue-property-decorator'
import UploadRepository from '@/services/repositories/upload'
import getRepository from '@/services'
import ContractRepository from '@/services/repositories/contract'
import ParamRepository from '@/services/repositories/params'
@Component
export default class ChangeStaffCode extends PoppupMixin {
  @Prop({}) bankContractNo!: string
  apiUpload!: UploadRepository
  apiContract!: ContractRepository
  apiParam!: ParamRepository
  fileList: any = []
  formData: Record<string, any> = {
    code: '',
    attachmentUrl: '',
    reason: '',
    status: 1
  }
  listReason: Record<string, any>[] = []
  listStatus: Record<string, any>[] = []
  fileType = ''
  get iconFileType(): string {
    let name = ''
    if (this.fileType.includes('.doc')) {
      name = 'word'
    }
    if (this.fileType.includes('.pdf')) {
      name = 'pdf'
    }
    if (this.fileType.includes('.xls')) {
      name = 'excel-fullcolor'
    }
    if (this.fileType.includes('.zip')) {
      name = 'zip'
    }
    return name
  }
  getConsultType(type: string): string {
    return type.includes('resign') ? 'NV nghỉ việc' : 'NV điều chuyển'
  }
  handleRemove(): void {
    // @ts-ignore
    this.$refs.upload?.clearFiles()
    this.fileList = []
    this.formData.attachmentUrl = ''
  }
  async handleChangeFile(file: Record<string, any>): Promise<void> {
    const formData = new FormData()
    if (file.status === 'ready') {
      file.type = file.raw.type
      this.fileType = file.name
      formData.append('files', file.raw)
      file.percentage = 1
      const processFunction = function (progressEvent: any) {
        let progress = (progressEvent.loaded / progressEvent.total) * 100
        file.percentage = progress
      }
      const data: Record<string, any> = {}
      data.data = formData
      data.progress = processFunction
      const result = await this.apiUpload.uploadFile(data)
      this.fileList[0] = {
        name: result.success[0].name,
        url: result.success[0].url
      }
      this.formData.attachmentUrl = result.success[0].url
      this.$forceUpdate()
    }
  }
  handleReset(): void {
    // @ts-ignore
    this.$refs.upload?.clearFiles()
    this.fileList = []
    this.formData = {
      code: '',
      attachmentUrl: '',
      reason: '',
      status: 1
    }
  }
  handleSubmit(type?: string): void {
    if (type == 'reset') {
      this.handleReset()
      this.setOpenPopup({
        popupName: 'change-staff-code',
        isOpen: false
      })
    } else {
      this.apiContract
        .changeConsultant(this.bankContractNo, this.formData)
        .then(() => {
          this.$emit('reset')
          this.$message.success('Cập nhập thành công')
          this.setOpenPopup({
            popupName: 'change-staff-code',
            isOpen: false
          })
        })
        .catch(err => this.$message.error(err.mesage))
    }
  }
  handleOpen(): void {
    this.init()
  }
  handleClose(): void {
    this.handleReset()
  }
  init(): void {
    this.apiParam.getChangeConsultantCode().then(res => {
      this.listReason = res
      if (this.listReason.length > 0) {
        this.formData.code = this.listReason[0].value
      }
    })
    // this.apiParam.getChangeConsultantStatus().then(res => (this.listStatus = res))
  }
  created(): void {
    this.apiUpload = getRepository('upload', {
      districtId: this.districtId
    })
    this.apiContract = getRepository('contract', {
      districtId: this.districtId
    })
    this.apiParam = getRepository('param', {
      districtId: this.districtId
    })
  }
}
