
























import PopupMixin from '@/mixins/popup'
import getRepository from '@/services'
import ContractRepository from '@/services/repositories/contract'
import { Component, Prop } from 'vue-property-decorator'
import DetailContractHistory from './DetailContractHistory.vue'
import EmptyBlock from '@/components/base/empty/EmptyBlock.vue'
@Component({ components: { DetailContractHistory, EmptyBlock } })
export default class EditHistory extends PopupMixin {
  @Prop({ required: false, type: Object, default: {} }) contract!: Record<string, any>
  apiContract!: ContractRepository
  logActive = 0
  param = {
    page: 1,
    limit: 100
  }
  dataLog: Record<string, any>[] = []
  dataDetail: Record<string, any> = {}
  // loadMore(): void {
  //   this.param.i += this.param.ps
  //   this.apiContract.getListPaymentLog(this.param).then(res => (this.dataLog = [...this.dataLog, ...res.data.content]))
  // }
  handleClickLog(index: number): void {
    this.logActive = index
    this.apiContract
      .getPaymentLogDetail(this.dataLog[this.logActive].logNumber, { contractNo: this.contract.contractNo, applyFormNo: this.contract.applyFormNo })
      .then(res => (this.dataDetail = res.data))
  }
  handleBackToDetail(): void {
    this.toggleDetailHistory(false)
    this.setOpenPopup({
      popupName: 'edit-history',
      isOpen: false
    })
  }
  toggleDetailHistory(toggle: boolean): void {
    this.setOpenPopup({
      popupName: 'contractDetailHistory',
      isOpen: toggle
    })
  }
  handleOpen(): void {
    this.init()
  }
  async init(): Promise<void> {
    await this.apiContract
      .getListPaymentLog({ ...this.param, contractNo: this.contract.contractNo, applyFormNo: this.contract.applyFormNo })
      .then(res => (this.dataLog = res.data.content))
      .catch(err => this.$message.error(err.message))
    if (this.dataLog[this.logActive]?.logNumber) {
      this.apiContract
        .getPaymentLogDetail(this.dataLog[this.logActive].logNumber, { contractNo: this.contract.contractNo, applyFormNo: this.contract.applyFormNo })
        .then(res => (this.dataDetail = res.data))
    }
  }
  handleClose(): void {
    this.dataDetail = {}
    this.dataLog = []
    this.logActive = 0
  }
  created(): void {
    this.apiContract = getRepository('contract', {
      districtId: this.districtId
    })
  }
}
