




































import { Component, Mixins, Watch, Prop } from 'vue-property-decorator'
import PopupMixin from '@/mixins/popup'
import getRepository from '@/services'
import { namespace } from 'vuex-class'
import ContractRepository from '@/services/repositories/contract'
import CategoryRepository from '@/services/repositories/category'
import EventBus from '@/utils/eventBus'
import { filter, trim, assign, debounce } from 'lodash'
const beUser = namespace('beUser')
@Component({ components: {} })
export default class TestPopup extends Mixins(PopupMixin) {
  @beUser.State('user') user?: Record<string, any>
  @Prop() checkCustomerType: any
  @Prop() dataProp: any
  apiContract?: ContractRepository
  api!: CategoryRepository
  idNumberLength = 0
  fullNameLength = 0
  @Watch('dataInput.customerIdNumber', { immediate: false })
  searchText(value: string): void {
    if ((value && value.length == 9) || (value && value.length == 12)) {
      this.listError.customerIdNumber = false
    } else {
      this.listError.customerIdNumber = true
    }
    this.idNumberLength = value.length
  }

  @Watch('dataInput.customerFullName', { immediate: false })
  changeName(value: string): void {
    const check = trim(value)
    this.fullNameLength = check.length
    if (check.length == 0) {
      this.listError.customerFullName = true
    } else {
      this.listError.customerFullName = false
    }
  }

  listError: Record<string, boolean> = {
    customerIdNumber: false,
    customerFullName: false
  }
  // if (rule.field === 'password' && trim(this.userData.password) === '') { callback(new Error('Mật khẩu không được để trống')) }

  rules: any = {
    customerCifCode: [{ min: 8, max: 8, message: 'CIF phải có 8 ký tự', trigger: 'change' }],
    customerFullName: [
      {
        required: true,
        message: this.fullNameLength == 0 ? 'Tên bảo hiểm không được để trống' : '',
        trigger: 'blur'
      }
    ],
    // customerMobilePhone: [{ required: true, pattern: /[0-9]{9},[0-9]{12}/, message: 'Please input phone', trigger: 'blur' }],
    customerIdNumber: [
      {
        required: true,
        message: this.idNumberLength == 0 ? 'Số CMND không được để trống' : '',
        // message: this.idNumberLength != 9 && this.idNumberLength != 12 ? 'Số CMND 9 hoặc 12 số' : null,
        trigger: 'change'
      }
      // {
      //   min: 9,
      //   max: 12,
      //   message: 'Số CMND 9 hoặc 12 số',
      //   trigger: 'change'
      // }
    ]
  }
  dataInput: any = {
    customerCifCode: '',
    customerFullName: '',
    customerIdNumber: '',
    customerMobilePhone: '',
    customerEmail: '',
    address: ''
  }
  changeValueIdNumber(value: string | number): void {
    console.log('query', value)
  }
  handleOpen(): void {
    if (this.dataProp.code == 'edit') {
      this.handleEditCustomer()
    }
    this.fullNameLength = 1
    //open popup
    // @ts-ignore
    this.$refs.dataInput.clearValidate()
    console.log(this.$route.name)
  }
  handleEditCustomer(): void {
    this.api.detailCustomer(this.dataProp.id).then(res => {
      if (res.customerCifCode) {
        this.dataInput.customerCifCode = res.customerCifCode
      }
      this.dataInput.customerFullName = res.customerFullName
      this.dataInput.customerIdNumber = res.customerIdNumber
      this.dataInput.customerMobilePhone = res.customerMobilePhone
      this.dataInput.customerEmail = res.customerEmail
      this.dataInput.address = res.address
    })
  }
  handleClose(): void {
    // @ts-ignore
    this.$refs.dataInput.clearValidate()
    this.dataInput.customerCifCode = ''
    this.dataInput.customerFullName = ''
    this.dataInput.customerIdNumber = ''
    this.dataInput.customerMobilePhone = ''
    this.dataInput.customerEmail = ''
    this.dataInput.address = ''
    this.setOpenPopup({
      popupName: 'addbmbh-popup',
      isOpen: false
    })
  }
  handleSave(): void {
    let a: any = this.$refs.dataInput
    a.validate((valid: any) => {
      if (valid) {
        if ((this.idNumberLength == 9 && this.fullNameLength > 0) || (this.idNumberLength == 12 && this.fullNameLength > 0)) {
          const params = {
            ...this.dataInput,
            customerFullName: this.dataInput.customerFullName.trim()
          }
          if (this.dataProp.code == 'edit') {
            this.api
              ?.updateCustomer(this.dataProp.id, params)
              .then((res: Record<string, any>) => {
                this.$message.success('Chỉnh sửa bên mua bảo hiểm thành công')
                //ban su kien sang Create Cost
                const data = {
                  customerCifCode: this.dataInput.customerCifCode,
                  customerFullName: this.dataInput.customerFullName,
                  id: this.dataProp.id
                }
                EventBus.$emit('pushIdBmbh', data)
                this.handleClose()
              })
              .catch(er => {
                this.$message.error(er.message)
              })
          }
          if (this.dataProp.code == 'add') {
            this.api
              ?.AddCustomer(params)
              .then((res: Record<string, any>) => {
                this.$message.success('Thêm bên mua bảo hiểm thành công')
                this.$emit('saveSuccess', res.data)
                this.handleClose()
              })
              .catch(er => {
                this.$message.error(er.message)
              })
          }
        }
      } else {
        console.log('error validate data!')
        return false
      }
    })
  }
  created(): void {
    this.apiContract = getRepository('contract', {
      districtId: this.user?.districts?.[0].id
    })
    this.api = getRepository('category', {
      districtId: this.user?.districts?.[0].id
    })
  }
}
